import React, { useEffect, useState } from "react"
import { ApiServer } from "../../config/ApiServer"
import { useInterval } from "../../hooks/useInterval"
// import { useInterval } from "../../hooks/useInterval"
import { useServerStatus } from "../../services/use-server-status"
import { AvatarImage } from "./AvatarImage"

export type AvatarProps = {
  server: ApiServer;
  onChange?: (apiServer: ApiServer, selected: boolean) => void;
}

const CHECK_INTERVAL = 10000

export const Avatar: React.FC<AvatarProps> = (props) => {
  const [selected, setSelected] = useState(false)
  const [enabled, setEnabled] = useState(false)
  const fetchServerStatus = useServerStatus()

  useEffect(() => {
    let mounted = true;
    fetchServerStatus(props.server).then((isAlive) => { mounted && setEnabled(isAlive) })
    return () => { mounted = false }
  }, [props.server, fetchServerStatus])

  const onClick = () => {
    if (!enabled) {
      return
    }
    const isSelected = !selected
    setSelected(isSelected)
    if (props.onChange) {
      props.onChange(props.server, isSelected)
    }
  }

  useInterval(() => {
    fetchServerStatus(props.server).then((isAlive) => setEnabled(isAlive))
    console.log("tick", props.server.name)
  }, CHECK_INTERVAL)

  return (
    <AvatarImage
      src={props.server.avatar}
      alt={props.server.id}
      onClick={onClick}
      selected={selected}
      enabled={enabled}
      data-testid="avatar-image"
    />
  )
}
