import React, { useState } from "react"
import styled from "styled-components"
import { ApiServer } from "../../config/ApiServer"
import { ApiServersConfigContext } from "../../config/ApiServersConfigContext"
import { Avatar } from "../avatar/Avatar"

const TargetSelectorContainer = styled.div`
  padding: 10px;
  margin: auto;
  width: fit-content;
`


export type TargetSelectorProps = {
  onServerSelected?: (selectedServers: ApiServer[]) => void;
}

export const TargetSelector: React.FC<TargetSelectorProps> = (props) => {
  const apiServers = React.useContext(ApiServersConfigContext)
  const [selectedServers, setSelectedServers] = useState(new Map())

  const onServerChange = (apiServer: ApiServer, selected: boolean) => {
    selectedServers.set(apiServer.id, selected)
    setSelectedServers(selectedServers)
    if (props.onServerSelected) {
      const targetServers = apiServers.filter((server) => selectedServers.get(server.id))
      props.onServerSelected(targetServers)
    }
  }

  return (
    <TargetSelectorContainer>
      {
        apiServers.map(apiServer =>
          <Avatar
            key={apiServer.id}
            server={apiServer}
            onChange={onServerChange}
          />
        )
      }
    </TargetSelectorContainer>
  )
}
