import React from "react";
import styled from "styled-components";
import { ToastData } from "./ToastData";


const ToastWrapper = styled.div`
  width: 100%;
  padding-left: 10px;
  animation: popFromBottom 0.5s ease-in-out;
  display: flex;
  flex-direction: row;
  justify-content: end;
  @keyframes popFromBottom {
    0% { transform: translateY(300px) scale(0); }
    100% { transform: translateY(0px) scale(1); }
  }
`

const ToastAvatar = styled.img`
  width: 54px;
  height: 54px;
  margin-top: 1em;
  margin-right: 1em;

  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;

  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
  -moz-box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
  box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
`

const ToastMessage = styled.div`
  background: ${({ theme }) => theme.toastColor};
  padding: 10px 50px 10px 10px;
  border-radius: 50px 50px 50px 50px ;
  margin: 20px -40px 20px 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  -webkit-box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
  -moz-box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
  box-shadow: 0 0 0 5px rgba(255,255,255,0.2), 0 0 0 4px #fff, 0 2px 5px 4px rgba(0,0,0,.2);
`

type ToastProps = {
  toast: ToastData;
}

export const Toast: React.FC<ToastProps> = (props) => (
  <ToastWrapper>
    <ToastMessage>{props.toast.content}</ToastMessage>
    <ToastAvatar src="./avatars/aigor.jpg"></ToastAvatar>
  </ToastWrapper >
);
