import { ApiClient } from "./api-client";

export class DefaultApiClient implements ApiClient {
  async get(url: string, headers?: { [key: string]: string; }): Promise<Response> {
    const options = {
      method: "GET",
      headers: headers
    }
    return fetch(url, options)
  }

  post<T>(url: string, headers?: { [key: string]: string; }): Promise<T> {
    throw new Error("Method not implemented.");
  }

}
