import styled from "styled-components";
import ThemeInterface from "../themes/ThemeInterface";

type AvatarImageProps = {
  selected: boolean;
  enabled: boolean;
  theme: ThemeInterface;
}

export const AvatarImage = styled.img`
width: 84px;
height: 84px;
margin-top: 1em;
margin-right: 1em;

-webkit-border-radius: 50%;
-moz-border-radius: 50%;
border-radius: 50%;

-webkit-box-shadow: 0 0 0 5px ${(props: AvatarImageProps) => props.selected ? props.theme.selectedColor : "rgba(255,255,255,0.2)"}, 0 0 0 4px #999, 0 2px 5px 4px rgba(0,0,0,.2);
-moz-box-shadow: 0 0 0 5px ${(props: AvatarImageProps) => props.selected ? props.theme.selectedColor : "rgba(255,255,255,0.2)"}, 0 0 0 4px #999, 0 2px 5px 4px rgba(0,0,0,.2);
box-shadow: 0 0 0 5px ${(props: AvatarImageProps) => props.selected ? props.theme.selectedColor : "rgba(255,255,255,0.2)"}, 0 0 0 4px #999, 0 2px 5px 4px rgba(0,0,0,.2);

opacity: ${(props: AvatarImageProps) => props.enabled ? 1 : .2};

`
