import { ApiServer } from "../config/ApiServer";

const postMessage = async (server: ApiServer, message: string): Promise<boolean> => {
  const url = `${server.url}/api/Speaker/say`
  let isSent = false
  try {
    const options = {
      method: "POST",
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ phrase: message })
    }
    await fetch(url, options)
      .then(() => isSent = true)
      .catch((e) => console.log(e))
  } catch (e) {
    console.log(e)
  }
  return isSent
}

export const useSendMessage = () => postMessage
