import React, { useState } from "react"
import { Header } from './components/header/Header';
import { TargetSelector } from './components/target-selector/TargetSelector';
import { ApiServer } from "./config/ApiServer";
import { MessageForm } from "./components/message-form/MessageForm";
import { Spinner } from "./components/loader/Spinner";
import { useToast } from "./components/toast/ToastProvider";
import { GlobalStyle } from "./components/themes";
import { useSendMessage } from "./services/use-send-message";

function App() {
  const [selectedServers, setSelectedServers] = useState([] as ApiServer[])
  const [loading, setLoading] = useState(false)
  const toast = useToast()
  const postMessage = useSendMessage()

  const sendMessage = (message: string): void => {
    setLoading(true)
    selectedServers.forEach((server) => {
      postMessage(server, message)
        .then((ok) => {
          toast.addToast(`mensaje enviado a ${server.name} ${ok? "ok": "error"}`)
        })
        .catch((e) => toast.addToast(e))
        .finally(() => setLoading(false))
    })
  }

  return (
    <>
      <GlobalStyle />
      <Header />
      <TargetSelector onServerSelected={setSelectedServers} />
      <MessageForm
        onMessageSend={sendMessage}
        enabled={selectedServers.length > 0}
      />
      <Spinner loading={loading}></Spinner>
      {loading}
    </>
  );
}

export default App;
