import React, { useContext } from "react";
import { createContext, useCallback, useState } from "react";
import { ToastContainer } from "./ToastContainer";
import { ToastData } from "./ToastData";


type ToastProviderInterface = {
  addToast: (content: string) => void,
  removeToast: (id: number) => void
}

const NotImplementedProvider: ToastProviderInterface = {
  addToast: (message: string) => { throw new Error("Not implemented") },
  removeToast: (id: number) => { throw new Error("Not implemented") }
}

const ToastContext = createContext<ToastProviderInterface>(NotImplementedProvider)

let nextId = 1

const ToastProvider: React.FC = ({ children }) => {
  const [toasts, setToasts] = useState([] as ToastData[])

  const addToast = useCallback(
    (content: string) => {
      const toast = { id: nextId++, content: content }
      setToasts((toasts) => [...toasts, toast])
      setTimeout(() => removeToast(toast.id), 3000)
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [setToasts],
  )

  const removeToast = useCallback(
    (id: number) => {
      setToasts((toasts) => toasts.filter(t => t.id !== id))
    },
    [setToasts]
  )

  return (
    <ToastContext.Provider value={
      {
        addToast,
        removeToast
      }
    }>
      <ToastContainer toasts={toasts} />
      {children}
    </ToastContext.Provider>
  )
}

const useToast = () => {
  const toastHelpers = useContext(ToastContext);

  return toastHelpers;
};

export { ToastContext, useToast };
export default ToastProvider
