import React, { useState } from "react"
import styled from "styled-components"

const MessageFormContainer = styled.div`
  padding: 10px;
  width: 90%;
  margin: auto;
`

const TextArea = styled.textarea`
  width: 100%;
  padding:10px;
  border:none ;
  font-size:1.5em;
  border-radius:10px;
  outline:none;
  transition: box-shadow .3s ease;
  display: block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  color: ${({theme})=> theme.text};
  :focus {
    box-shadow: 0px 0px 0px 4px ${({ theme }) => theme.selectedColor};
  }
`
const SendButton = styled.button`
  width: 100%;
  padding: 10px;
  background-color: ${({ theme }) => theme.selectedColor};
  display: block;
  border: 0px;
  border-radius: 10px;
  font-size: 1.8em;
  margin-top: 10px;
  color: ${({ theme }) => theme.text};
  transition: opacity .3s ease;
  :disabled {
    opacity: .1
  }
`

type MessageFormProps = {
  onMessageSend: (message: string) => void;
  enabled: boolean;
}

export const MessageForm: React.FC<MessageFormProps> = (props) => {
  const [message, setMessage] = useState("")
  const sendForm = () => {
    props.onMessageSend(message)
    setMessage("")
  }

  return (
    <MessageFormContainer>
      <TextArea
        rows={5}
        value={message}
        onChange={(event) => setMessage(event.target.value)}
        placeholder="¿Qué quieres que les diga?"
      >
      </TextArea>
      <SendButton disabled={!props.enabled || message.length < 1} onClick={sendForm}>enviar</SendButton>
    </MessageFormContainer>
  )
}
